import React from "react";
import QuickLinks from "../QuickLink/QuickLinks";
import PostsPage from "../Events/PostsPage";
import ContactUs from "../Contact/ContactUs";
import MainStory from "../MainStory/MainStory";
import Project from "../Projects/Project";
import Slider from "../Slider/Slider";
import Footer from "../Footer/Footer";
// import img1 from '../../images/main.jpg';

const Home = (props) => {

    return (
        <div>
            <Slider displayPerPage = {1}></Slider>
            {/* <img src={img1} alt="" className="fixed_img"/> */}
			<QuickLinks/>
            <MainStory/>
			<Project/>
			<PostsPage/>
			{/* <GalleryPage/> */}
            <Slider displayPerPage = {7} mode = {2}></Slider>
			<ContactUs/>
            <Footer/>
	  	</div>
      );
  };

 
export default Home;
