/** @jsx jsx */
import React, {useState} from "react";
import {css,jsx} from '@emotion/core';
import SliderContent from './SliderContent';
import Slide from './Slide';
import SingleSlide from './SingleSlide';
import Arrow from './Arrow';
import Dots from './Dots';
import getImageData from '../imageData';
import { propTypes } from "react-bootstrap/esm/Image";

const Slider = (props) => {
    const { displayPerPage, mode } = props;
    console.log(displayPerPage);
    const windowWidth = () => window.innerWidth;
    const getWidth = () => window.innerWidth/displayPerPage;
    const size = 100/displayPerPage;
    const frameHeight = 80/displayPerPage;
    let images = getImageData();
    const displayDots = images.length > displayPerPage;
    const [state, setState] = useState({
        activeIndex: 0,     
        translate: 0,
        transition: 0.45,
        selectedImage: images[0]
    })
 
    const SliderCSS = css`
    position: relative;
    height: ${frameHeight}vh;
    width: 100vw;
    margin: 0 auto;
    overflow: hidden;
    `

    const ChosenSliderCSS = css`
    position: absoloute;
    height: 80vh;
    width: 100vw;
    margin: 0 auto;
    overflow: hidden;
    margin-bottom: 10px;
    `

    console.log('============');
    console.log(images);
    const {translate, transition, activeIndex, selectedImage} = state;

    const nextSlide=()=>{
        if(activeIndex === images.length - (displayPerPage)){
            return setState({
                ...state,
                translate: 0,
                activeIndex: 0
            })
        }

        setState({
            ...state,
            activeIndex: activeIndex + 1,
            translate: (activeIndex + 1) * getWidth()
        })
    }

    const prevSlide=()=>{
        if(activeIndex === 0){
            return setState({
                ...state,
                translate: (images.length - displayPerPage) * getWidth(),
                activeIndex: images.length - displayPerPage 
            })
        }

        setState({
            ...state,
            activeIndex: activeIndex - 1,
            translate: (activeIndex - 1) * getWidth()
        })
    }
    
    const updateSlide=(event)=>{
        setState({
            ...state,
            selectedImage: event.target.id
        })
    }

    return (
        <div>
            {mode == '2' ? 
                <div css ={ChosenSliderCSS}>
                    {/* {images[activeIndex]} */}
                    <SingleSlide 
                        key={activeIndex}
                        content = {selectedImage}
                        size = {100}>
                    </SingleSlide>
                </div> : null}
            <div css={SliderCSS}>
                <SliderContent 
                    translate={translate}
                    transition={transition}
                    width={getWidth() * images.length}>
                        {images.map(slide => (
                            <Slide 
                                key={slide}
                                content ={slide}
                                size = {size}
                                handleClick={updateSlide}>
                            </Slide>
                            ))}
                </SliderContent>
                {displayDots ? <Arrow direction='left' handleClick={prevSlide}/> : null}
                {displayDots ? <Arrow direction='right' handleClick={nextSlide}/> : null}
                {displayDots ?  <Dots images={images} activeIndex={activeIndex}/> : null}
            </div>
        </div>
    );
  };


export default Slider;
